<template>
  <el-container>
    <el-main>
      <el-tooltip placement="left">
        <template #content>
          Left Click to Add
          <br />Right Click to Remove
        </template>
        <el-button @click="addColumn" @contextmenu.prevent="removeColumn" type="info">ADD COLUMN</el-button>
      </el-tooltip>
      <el-button @click="refreshTable" type="primary">REFRESH</el-button>
      <el-button @click="newBook" type="success">ADD NEW BOOK</el-button>
      <el-button @click="toggleDialog" type="danger">PRINT</el-button>
      <el-divider></el-divider>

      <el-row :key="refreshKey">
        <el-col v-for="m in column" :span="24/column" :key="m">
          <el-table :data="getdata(m)" border style="width: 100%">
            <el-table-column prop="name" label="Name">
              <template #default="scope">
                <el-button type="text" @click="scope.row.amount++">{{scope.row.name}}</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="amount" label="amount" width="120" class-name="no-padding">
              <template #default="scope">
                <el-input type="number" placeholder="100" v-model.number="scope.row.amount">
                  <template #append>
                    <el-popover placement="top" :width="40" trigger="click">
                      <template #reference>
                        <el-button tabindex="-1" :icon="Plus"></el-button>
                      </template>
                      <el-button-group size="small" style="margin-bottom:8px;">
                        <el-button
                          @click="handleCustomAdd(k*5, scope.row)"
                          v-for="k in 4"
                          :key="k"
                        >{{k*5}}</el-button>
                      </el-button-group>
                      <el-input
                        type="number"
                        placeholder="100"
                        :autofocus="true"
                        v-focus
                        v-model.number="tempValue"
                        @keyup.enter="handleAdd(scope.row)"
                      >
                        <template #append>
                          <el-button @click="handleAdd(scope.row)" :icon="Check"></el-button>
                        </template>
                      </el-input>
                    </el-popover>
                  </template>
                </el-input>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-main>

    <el-dialog v-model="dialogVisible" title="মাসিক হরেকৃষ্ণ সমাচার">
      <div id="printable">
        <h4 contenteditable="true" style="text-align:center">জানুয়ারি ২০২২</h4>
        <el-table show-summary border :data="printingData" sum-text="মোট">
          <el-table-column type="index" width="60" label="নং" />
          <el-table-column property="name" label="নাম"></el-table-column>
          <el-table-column property="amount" label="পরিমাণ" width="100"></el-table-column>
        </el-table>
      </div>
      <el-button-group class="btn-group">
        <el-button :icon="Printer" type="primary" plain @click="printMe"></el-button>
        <el-button :icon="DocumentCopy" type="success" plain @click="saveMonth"></el-button>
      </el-button-group>
    </el-dialog>
  </el-container>
</template>

<script>
import { ref, onBeforeMount, unref } from "vue";
import { ElMessageBox, ElMessage } from "element-plus";
import { Plus, Check, Printer, DocumentCopy } from "@element-plus/icons-vue";
import htmlToPaper from "./plugins/print-html";

export default {
  name: "App",
  setup() {
    const tempValue = ref(null);
    const dialogVisible = ref(false);
    const refreshKey = ref(1);
    const column = ref(4);
    const printingData = ref([]);

    const toggleDialog = () => {
      dialogVisible.value = !dialogVisible.value;
      printData();
    };

    let tableData = ref([]);

    const refreshTable = () => {
      // location.reload();
      refreshKey.value += refreshKey.value;
    };

    onBeforeMount(async () => {
      const books = await fetch(`${location.href}/books.json`);
      tableData.value = await books.json();
    });

    const getdata = i => {
      if (tableData.value.length) {
        const count = Math.ceil(tableData.value.length / column.value);
        if (i > 0 && i <= column.value) {
          return tableData.value.slice(count * (i - 1), count * i);
        }
      }
      return [];
    };

    const handleAdd = data => {
      data.amount = Number(data.amount) + tempValue.value;
      tempValue.value = "";
    };

    const handleCustomAdd = (num, data) => {
      tempValue.value = num;
      handleAdd(data);
    };

    const addNewBook = value => {
      let book = { name: value, amount: "" };
      // Add Api call to add book to JSON file
      tableData.value.push(book);
    };

    const addColumn = () => {
      if (column.value < 8) {
        column.value += 2;
      }
    };

    const removeColumn = () => {
      if (column.value > 2) {
        column.value -= 2;
      }
    };

    const saveMonth = () => {
      ElMessage({ type: "danger", message: "Sorry! Not Implemented Yet" });
    };

    const newBook = () => {
      ElMessageBox.prompt("Please input book name", "Add New Book", {
        confirmButtonText: "OK",
        cancelButtonText: "Cancel"
      })
        .then(({ value }) => {
          addNewBook(value);
          ElMessage({
            type: "success",
            message: `${value} - Book Successfully Added.`
          });
        })
        .catch(e => console.log(e));
    };

    const printData = () => {
      printingData.value = tableData.value.filter(r => r.amount);
    };

    const printMe = () => {
      htmlToPaper("printable");
    };

    return {
      refreshTable,
      printMe,
      printData,
      newBook,
      tempValue,
      tableData,
      getdata,
      Plus,
      dialogVisible,
      Check,
      handleAdd,
      handleCustomAdd,
      toggleDialog,
      Printer,
      printingData,
      DocumentCopy,
      saveMonth,
      refreshKey,
      column,
      addColumn,
      removeColumn
    };
  }
};
</script>

<style>
body {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app {
  text-align: center;
  color: #2c3e50;
}
.btn {
  margin-top: 100px;
}
input[type="number"] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.el-input-group__append {
  padding: 0 10px;
}

.btn-group {
  margin-top: 1rem;
  float: right;
}
@media print {
  .not-print {
    display: none;
  }
  .printable {
    display: block;
  }
}
</style>
